@import url(https://fonts.googleapis.com/css?family=Open+Sans:700,300);
body {
  margin: 0;
  font-family: 'Satoshi', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

title {
  font-family: 'Raleway', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  background-color: #f8f3ed;
  display: block;
  padding: 2rem;
  min-height: 100vh;
}

.App-logo {
  height: 10vmin;
  margin-top: 2rem;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 2rem;
  font-size: calc(25px + 2vmin);
  font-weight: 900;
  font-family: 'Raleway';
  color: #f1889f;
}

.App-link {
  color: #61dafb;
}

.btn {
  background-color: #212529;
  border: 0;
  padding: 0.5rem 4rem;
  border-radius: 0.8rem;
  font-family: 'Satoshi';
  font-weight: 400;
  font-size: medium;
  color: #f8f3ed;
  cursor: pointer;
}

.btn:hover {
  background-color: #4b4949;
}

.buttons {
  width: 20rem;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  display: grid;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.frame {
  position: absolute;
  top: 50%;
  left: 70%;
  width: 400px;
  height: 400px;
  margin-top: -200px;
  margin-left: -200px;
  border-radius: 2px;
	box-shadow: 4px 8px 16px 0 rgba(0,0,0,0.1);
	overflow: hidden;
   background: #43389F;
  background: linear-gradient(to top right, #43389F 0%, #4ec6ca 100%); 
  color: #333;
	font-family: 'Courier New', 'Courier', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.center {
  position: absolute;
  top: 55%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.number{
	position: relative;
	height: 100px;
	width: 200px;
}

.one-one {
	position: absolute;
	top: 2px;
	left: -5px;
	height: 40px;
	width: 25px;
	z-index: -1;
	-webkit-transform: rotate(50deg);
	        transform: rotate(50deg);
	background: white;
	border-radius: 3px;
	box-shadow: 0 0 13px 0 rgba(0,0,0,0.2)
}

.one-two {
	z-index: 5px;
	position: absolute; 
	top: 1px; 
	left: 10px;
	border-radius: 3px;
	box-shadow: 0 0 13px 0 rgba(0,0,0,0.2);
	height: 98px;
	width: 23px;
	
	background: white;
}

.zero-one, .zero-two{
	position: absolute;
	top: 0;
	z-index: -2;
	left: 22px;
	width: 55px;
	height: 55px;
	border: 23px solid white;
	border-radius: 100%;
	box-shadow: 0 0 13px 0 rgba(0,0,0,0.2);
}

.zero-two{
	left: 110px;
	z-index: -3;
}

.big {
	position: relative;
	top: -10px;
	font-size: 82px;
	font-weight: 700;
	color: white;
	left: 8px;
	text-transform: uppercase;
}

.small {
	top: -28px;
	position: relative;
	color: white;
	font-size: 24px;
	left: 8px;
	font-weight: 700;
}
